<template>
  <div class="page page_fashion_gallery bg_white">
    <section class="col-xs-12 page">
      <div class="table">
        <div class="table-cell">
          <div class="row">
            <div class="col-md-3">
              <div class="image-container">
                <transition name="slide-top">
                  <app-block-image
                          v-for="(value, key, index) in page_gallery_fashion['projects']"
                          :key="index"
                          v-if="key == currentProject1"
                          class="bg-image image gallery_element"
                          :bg_image="value.images[0]"
                  ></app-block-image>
                </transition>
              </div>
            </div>
            <div class="col-md-3">
              <div class="image-container">
                <transition name="slide-top">
                  <app-block-image
                          v-for="(value, key, index) in page_gallery_fashion['projects']"
                          :key="index"
                          v-if="key == currentProject2"
                          class="bg-image image gallery_element"
                          :bg_image="value.images[1]"
                  ></app-block-image>
                </transition>
              </div>
            </div>
            <div class="col-md-3">
              <div class="row">
                <div class="col-md-12">
                  <div class="image-container bg-half-block">
                    <transition name="slide-top">
                      <app-block-image
                        v-for="(value, key, index) in page_gallery_fashion['projects']"
                        :key="index"
                        v-if="key == currentProject3"
                        class="bg-image image gallery_element bg-half-block"
                        :bg_image="value.images[2]"
                      ></app-block-image>
                    </transition>
                  </div>
                </div>
                <div class="col-md-12">
                  <div class="image-container bg-half-block">
                    <transition name="slide-top">
                      <app-block-image
                              v-for="(value, key, index) in page_gallery_fashion['projects']"
                              :key="index"
                              v-if="key == currentProject4"
                              class="bg-image image gallery_element bg-half-block"
                              :bg_image="value.images[3]"
                      ></app-block-image>
                    </transition>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-3">
              <div class="row">
                <div class="col-md-12">
                  <div class="bg-half-block gallery_element gallery_text-block">
                    <h3 class="gallery__title">{{page_gallery_fashion[$route.params.lang]['text']['title']}}</h3>
                    <div class="pr">
                      <transition name="fade">
                        <p
                          class="gallery__description"
                          v-for="(value, key, index) in page_gallery_fashion['projects']"
                          :key="index"
                          v-if="key == currentProject">
                          {{value.description}}
                        </p>
                      </transition>
                    </div>
                  </div>
                </div>
                <div class="col-md-12">
                  <div class="image-container bg-half-block">
                    <transition name="slide-top">
                      <app-block-image
                        v-for="(value, key, index) in page_gallery_fashion['projects']"
                        :key="index"
                        v-if="key == currentProject5"
                        class="bg-image image gallery_element bg-half-block"
                        :bg_image="value.images[4]"
                      ></app-block-image>
                    </transition>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <button class="pull-left gallery__back-button button__back" @click="navigateBack">
      {{page_gallery_fashion[$route.params.lang]['buttons']['back']}}
    </button>
    <button class="button__scroll-down" @click="scrollDown">
      {{page_gallery_fashion[$route.params.lang]['buttons']['scroll']}}
    </button>
  </div>
</template>

<script>
  import block_image from '../global/image_block.vue'
  export default {
    data(){
      return {
        currentProject: 'project_1',
        //duplicating to make delay
        currentProject1: 'project_1',
        currentProject2: 'project_1',
        currentProject3: 'project_1',
        currentProject4: 'project_1',
        currentProject5: 'project_1',
        page_gallery_fashion: {
          ru: {
            text: {
              title: 'Весна-лето 2016',
            },
            buttons: {
              back: 'Назад',
              scroll: 'Scroll Down'
            }
          },
          en: {
            text: {
              title: 'Spring-Summer 2016'
            },
            buttons: {
              back: 'Back',
              scroll: 'Scroll Down'
            }
          },
          projects: {
            project_1: {
              description: "Шифоновое платье, выполненное в ярко желтом цвете, приталенного силуэта.",
              images: [
                '/static/img/Fashioncollection_1.png',
                '/static/img/Fashioncollection_2.png',
                '/static/img/Fashioncollection_3.png',
                '/static/img/Fashioncollection_4.png',
                '/static/img/Fashioncollection_5.png']
            },
            project_2: {
              description: "Замечательное платье 2",
              images: [
                '/static/img/Fashioncollection_2.png',
                '/static/img/Fashioncollection_1.png',
                '/static/img/Fashioncollection_5.png',
                '/static/img/Fashioncollection_4.png',
                '/static/img/Fashioncollection_3.png']
            }
          }
        },
      }
    },
    components: {
      appBlockImage: block_image,
    },
    watch: {
      currentProject(value){//change slides with some delay
        setTimeout(function(){this.currentProject1 = value}.bind(this), 0);
        setTimeout(function(){this.currentProject2 = value}.bind(this), 50);
        setTimeout(function(){this.currentProject3 = value}.bind(this), 100);
        setTimeout(function(){this.currentProject4 = value}.bind(this), 150);
        setTimeout(function(){this.currentProject5 = value}.bind(this), 200);
      }
    },
    methods: {
      navigateBack(){
        this.$route.push(window.history.back());
      },
      scrollDown(){
        if (this.currentProject == 'project_1') this.currentProject = 'project_2'
        else if (this.currentProject == 'project_2') this.currentProject = 'project_1';
      }
    },
  }
</script>


<style lang="sass" src="../../assets/style/styleguide.sass"></style>
<style lang="sass" src="../../assets/style/global.sass"></style>
<style lang="sass" src="../../assets/style/fashion_gallery.sass"></style>


